button.tertiary {
  background-color: transparent !important;
}

.ui.corner.label .icon {
  position: absolute;
  top: 0.64285714em;
}

.ui.corner.label.right .icon {
  right: 0.48571429em;
  left: auto;
}

.ui.corner.label.left .icon {
  left: 0.48571429em;
  right: auto;
}

.ui.card.mentor-card {
  justify-content: space-between;
}

.ui.card.mentor-card:after {
  display: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.device-desktop .mobile,
.device-mobile .desktop {
  display: none !important;
}
body {
  font-family: 'Lato', sans-serif;
  background-color: #f8f8f8;
}

.app {
  padding-bottom: 70px;
}

.device-desktop .filters-outer {
  text-align: center;
}

.device-desktop .filters {
  display: inline-block;
  text-align: left;
}

.device-mobile .filters {
  padding: 0 20px;
}

.main-header {
  padding: 30px;
  max-width: 960px;
  margin: 0 auto;
}

.device-mobile .main-header {
  padding: 20px;
}

.main-header .logo {
  display: flex;
  color: #a1a1a1;
  justify-content: space-between;
}

.main-header h1 {
  font-size: 48px;
}

.social {
  margin-top: 10px;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: #65d6af;
  padding: 10px 0;
  z-index: 100;
}

footer a,
footer a:hover {
  padding: 0 3px;
  color: #fff;
  text-decoration: underline;
}
.search-wrapper {
  position: relative;
  margin: 10px 0;
}

.device-desktop .search-wrapper {
  display: inline-block;
}

.device-desktop .search-wrapper:not(:last-child) {
  margin-right: 5px;
}

.device-mobile .search-input > .input {
  display: flex;
}
.mentors-wrapper {
  margin-top: 15px;
}

.device-desktop .mentors-wrapper {
  margin-right: 1rem;
  margin-left: 1rem;
}

.device-mobile .mentors-wrapper {
  -webkit-transform: translateY(-105px);
          transform: translateY(-105px);
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}

.device-mobile .mentors-wrapper.active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.device-mobile .mentors-wrapper > .mentors-cards {
  margin: 0;
  padding: 0 20px;
}

.device-desktop .mentor-card.mentor-card.mentor-card {
  width: 350px;
}

.device-mobile .mentor-card.mentor-card.mentor-card {
  width: 100%;
  margin-right: 0;
  margin-left: 0;
}

.mentor-card header {
  display: flex;
}

.mentor-card .details {
  padding: .35em 1em;
}

.mentor-card .tags {
  margin-bottom: -8px;
}

.mentor-card .description {
  /* 2 lines min */
  height: 38px;
}

.mentor-card img {
  height: 100px;
}

.mentor-card .mentor-tag {
  border-radius: 0;
  background-color: #68d5b1;
  color: #fff;
  margin: .14285714em;
  text-transform: lowercase;
}

.mentor-card .mentor-quote {
  color: #68d5b1;
}

.ui.button.channel-link {
  padding-right: 0;
  padding-left: 0;
}

